//   https://colorscheme.ru/color-converter.html

$black:                       hsl(0, 0%, 0%);
$gray-darkest:                hsl(0, 0%, 10%);
$gray-darker:                 hsl(0, 0%, 20%);
$gray-darken:                 hsl(0, 0%, 30%);
$gray-dark:                   hsl(0, 0%, 40%);
$gray:                        hsl(0, 0%, 50%);
$gray-light:                  hsl(0, 0%, 60%);
$gray-lighten:                hsl(0, 0%, 70%);
$gray-lighter:                hsl(0, 0%, 80%);
$gray-lightest:               hsl(0, 0%, 90%);
$white:                       hsl(0, 0%, 100%);

$color-success:               hsl(120, 39%, 54%);
$color-danger:                hsl(2, 64%, 58%);
$color-warning:               hsl(35, 84%, 62%);

//$color-vk:                  #4a76a8;
$color-fb:                    #4267b2;
$color-tw:                    #40A2F5;
$color-gh:                    #24292e;


//  

$main-color:                  #df701e;
$main-color--trainers:        #0d7b33;
$main-color--business:        #225c9b;
$main-color--portal:          #920bd1;
$specblue-color:              #3168f4;

$color-main:                  $main-color;

$text-color:                  #191a3f;
$title-color:                 #0a091c;
$text-muted:                  #bebebe;

$body-bg:                     #ffffff;

$link-color:                  currentColor;
$link-color--hover:           $main-color;

$border-color:                #f3f3f3;

//  

$font-size:                   16px;

$font-size--h1:               28px;
$font-size--h2:               26px;
$font-size--h3:               22px;
$font-size--h4:               18px;
$font-size--h5:               16px;
$font-size--h6:               16px;

$font-size-sm:                14px;

$line-height:                 1.5;

//$typo-margin-vertical:        1em;

$font-family:                -apple-system, BlinkMacSystemFont, 'Inter', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif;
//$font-family--headings:      -apple-system, BlinkMacSystemFont, 'Roboto', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif;
$font-family--monospace:     SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace;


// 

$screen-xs:                   0;
$screen-sm:                   480px;
$screen-md:                   768px;
$screen-lg:                   992px;
$screen-xl:                   1440px;
$screen-xxl:                  1800px;

$container-sm:                100%;
$container-md:                100%;
$container-lg:                100%;
$container-xl:                $screen-xl - 20;
$container-xxl:               $screen-xxl - 20;


//  

$grid-columns:                12;
$grid-gutter-width:           24px;


// 

$field-padding-vertical:      5px;
$field-padding-horizontal:    12px;

$border-radius:               0;
$opacity:                     0.35;
$transition-time:             0.3s;
$shadow:                      0 4px 2px -2px rgba(0, 0, 0, 0.3);

$menu-desktop-width:          $screen-xl;
$toggler-part-height:         3px;
$toggler-part-bg-color:       #000;

$z-index-modal:               100;
