$text-color--muted:           hsl(0, 0%, 50%) !default;

$font-size--small:            0.75em !default;

$line-height:                 1.375em !default;

.field-actions {

  $block-name:                &; // #{$block-name}__element

  display: block;
  margin-top: $line-height;
  margin-bottom: 20px;

  .btn {
    width: 100%;
    //margin-bottom: 20px;
  }

  &__text {
    display: block;
    //margin-top: ($line-height / 2);
    //margin-bottom: ($line-height / 2);
    font-size: $font-size;
    color: $text-muted;
    line-height: 1.2em;
  }
}
